<template>
  <v-form v-model="valid" ref="userForm">
    <v-text-field
      label="用户名"
      v-model="user.name"
      :rules="[v => !!v || '用户名不能为空']"
      :counter="10"
      required
    />
    <v-text-field
      label="邮箱"
      v-model="user.email"
      :rules="[v => !!v || '邮箱不能为空']"

      required
    />
    <v-text-field
      label="密码"
      v-model="user.password"
      :rules="[v => !!v || '密码不能为空']"

      required
    />
    <v-select
      :items="items"
      v-model="user.role"
      label="角色*"
    ></v-select>

    <v-layout class="my-4">
      <v-btn @click="submit" color="primary">提交</v-btn>
      <v-btn @click="clear" color="warning">重置</v-btn>
    </v-layout>
  </v-form>
</template>

<script>
  export default {
    name: "user-form",
    props: {
      oldUser: Object,
      isEdit: {
        type: Boolean,
        default: false
      },
      show: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        items:['普通用户', '管理员'],
        valid:false,
        user: {
          uid:"",
          password:"",
          name: "",
          email:"",
          role:""
        }
      }
    },
    watch: {
      oldUser:{
        immediate: true,
        deep:true,
        handler(val){
          this.user=Object.deepCopy(val);
        }
      }
    },
    methods: {
      submit() {
        // 表单校验
        if (this.$refs.userForm.validate()) {
          switch (this.user.role) {
            case "普通用户":
              this.user.rid=1;
              break;
            case "管理员":
              this.user.rid=2;
              break;
            default:
              this.user.rid=1;
          }
          if(this.isEdit)
            this.user.oldemail=this.oldUser.email;
          this.$http.post(
            this.isEdit ? '/UserManage/edit' : '/UserManage/add',
            this.$qs.stringify(this.user)
          ).then(() => {
            // 关闭窗口
            this.$message.success("保存成功！");
            this.closeWindow();
          }).catch(() => {
            this.$message.error("保存失败:邮箱已存在！");
          });
        }
      },
      clear() {
        // 重置表单
        this.$refs.userForm.reset();
      },
      closeWindow(){
        this.$emit("close");
      }
    }
  }
</script>

<style scoped>

</style>



